<template>
  <div class="header">
    <Header>
      <template #headline>
        <logo />
        <navigation />
      </template>
      <template #content>
        <ul class="stories">
          <li
            class="stories-item"
            v-for="item in trendings"
            :key="item.id"
          >
            <story-user-item
              v-bind="getFeedData(item)"
              @onPress="$router.push({name: 'stories', params: {initialSlide: item.id}})"
            />
          </li>
        </ul>
      </template>
    </Header>
  </div>
  <user-content />
  <pre>{{ trendings }}</pre>
</template>

<script>
import Header from '@/components/header/Header.vue'
import storyUserItem from '@/components/storyUserItem/storyUserItem.vue'
import stories from './data.json'
import userContent from '@/components/userContent/userContent.vue'
import navigation from '@/components/navigation/navigation.vue'
import logo from '@/components/logo/logo.vue'
import * as api from '../../components/api'
import { mapActions, mapState } from 'vuex'
export default {
  name: 'Feeds',
  components: {
    Header,
    storyUserItem,
    userContent,
    navigation,
    logo
  },
  computed: {
    ...mapState({
      trendings: (state) => state.trendings.data
    })
  },
  async created () {
    try {
      const { data } = await api.trendings.getTrendings()
      this.items = data.items
    } catch (error) {
      console.log(error)
    }
  },
  data () {
    return {
      stories,
      items: []
    }
  },
  methods: {
    handlePress () {
      console.log(1)
    },
    getFeedData (item) {
      return {
        avatar: item.owner.avatar_url,
        username: item.owner.login
      }
    },
    ...mapActions({
      fetchTrendings: 'trendings/fetchTrendings'
    })
  },
  mounted () {
    this.fetchTrendings()
  }
}
</script>

<style src="./feeds.scss" lang="scss" scoped></style>
